<template>
  <div class="vehicle-body">
    <div class="vehicle-line">
      <div class="vehicle-page">{{PageName}}</div>
      <div class="vehicle-button">
        <div class="vehicle-button-input">
          <el-input class="input-style" v-model="searchForm.licensePlate" size="medium" placeholder=""/>
          <el-input class="input-style" v-model="searchForm.obuId" size="medium" placeholder="OBU ID"/>
          <el-select clearable style="width: 150px; margin-top: 4px" v-model="searchForm.siteName" placeholder="">
            <el-option v-for="(item) in this.store.siteAllData" :key="item.siteId" :label="item.siteName" :value="item.siteName"/>
          </el-select>
          <el-date-picker class="input-style" type="datetime" format="yyyy-MM-dd" value-format="yyyy-MM-dd" placeholder="" v-model="searchForm.startTime"></el-date-picker>
          -
          <el-date-picker class="input-style" type="datetime" format="yyyy-MM-dd" value-format="yyyy-MM-dd" placeholder="" v-model="searchForm.endTime" ></el-date-picker>
          <el-button class="add-margin" type="primary" icon="el-icon-search" @click="getQuery"></el-button>
          <el-button style="width: 80px" type="primary" @click="updateVehicleData"></el-button>
        </div>
        <el-button style="width: 100px" type="primary" @click="dialogAddVehicleVisible = true"></el-button>
      </div>
    </div>
    <div class="vehicle-list">
      <el-table :data="getOrSearch === 0? vehicleData:vehicleSearchData " style="width: 100%">
        <el-table-column align="center" label="" width="100" prop="virId" sortable/>
        <el-table-column align="center" label="" width="200" prop="licensePlate" sortable/>
        <el-table-column align="center" label="OBU ID" min-width="200" prop="obuId" sortable/>
        <el-table-column align="center" label="" min-width="200" prop="startTime" sortable/>
        <el-table-column align="center" label="" min-width="200" prop="endTime" sortable/>
        <el-table-column align="center" label="" width="150" prop="frequency"/>
        <el-table-column align="center" label="" width="150" prop="siteName" sortable/>
      </el-table>
      <!--  -->
      <el-pagination
              layout="total, sizes, prev, pager, next, jumper" style="text-align: center;"
              @size-change="sizeChange" @current-change="currentChange" :total="totalCount">
      </el-pagination>
    </div>

    <!--  -->
    <el-dialog title="" :visible.sync="dialogAddVehicleVisible" width="40%">
      <el-form ref="form" :model="vehicleForm" label-width="80px" >
        <el-form-item label="">
          <el-input v-model="vehicleForm.licensePlate" style="width: 300px"/>
        </el-form-item>
        <el-form-item label="OBU ID">
          <el-input v-model="vehicleForm.obuId" style="width: 300px"/>
        </el-form-item>
        <el-form-item label="">
          <el-col :span="11">
            <el-date-picker v-model="date1" type="datetime" format="yyyy-MM-dd HH:mm:ss" value-format="yyyy-MM-dd HH:mm:ss" placeholder=""/>
          </el-col>
        </el-form-item>
        <el-form-item label="">
          <el-col :span="11">
            <el-date-picker v-model="date3" type="datetime" format="yyyy-MM-dd HH:mm:ss" value-format="yyyy-MM-dd HH:mm:ss" placeholder=""/>
          </el-col>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="vehicleForm.frequency" style="width: 300px"/>
        </el-form-item>
        <el-form-item label="ID">
          <el-select v-model="vehicleForm.siteId" placeholder="">
            <el-option v-for="item in store.siteAllData" :key="item.siteId" :label="item.siteName" :value="item.siteId"/>
          </el-select>
        </el-form-item>
        <el-form-item label="">
          <el-select v-model="vehicleForm.devId" placeholder="">
            <el-option v-for="item in store.deviceAllData" :key="item.devId" :label="item.devName" :value="item.devId"/>
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="addVehicle()"></el-button>
          <el-button @click="dialogVehicleVisible = false"></el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
  </div>
</template>

<script>
import {store} from "@/store/store";
import {getAllVehicleServeData,getCurrentVehicleServeData,addVehicleServeData,searchVehicleServeData} from "@/network/vehicle";

export default {
  name: 'CarInfoRecord',
  inject:['reload'],
  data () {
    return {
      store,
      search: '',
      PageName: '',
      startDate:'',
      endDate:'',
      date1:'',
      time1:'',
      date3:'',
      time3:'',
      pageNum:'1',
      pageSize:'10',
      totalCount:0,
      vehicleData:[], // 
      vehicleSearchData:[], // 
      getOrSearch:0,
      dialogAddVehicleVisible:false,
      searchForm:{
        currentPage:1,
        pageSize:10,
        obuId:'',
        licensePlate:'',
        siteName:'',
        startTime:'',
        endTime:'',
      },
      vehicleForm: {   // 
        obuId:'',
        licensePlate:'',
        startTime:'',
        endTime: '',
        frequency: '', // 
        siteId: '',
        devId: '',
        trackRecord:'',
        rawData:'',
      },
    }
  },

  mounted () {
    this.updateVehicleData()
  },

  methods: {
    /**
     * 
     */
    updateVehicleData () {
      getAllVehicleServeData().then(res => {
        this.vehicleData = res.data.rows
        this.totalCount = res.data.totalCount
        this.store.vehicleData = res.data.rows
        this.getOrSearch = 0
      }).catch(err => {
        console.log(err)
      })
    },

    /**
     * 
     */
    addVehicle () {
      this.vehicleForm.startTime = this.date1
      this.vehicleForm.endTime = this.date3
      addVehicleServeData(this.vehicleForm).then(res => {
        this.reload()
      }).catch(err => {
        console.log(err)
      })
      this.dialogAddVehicleVisible = false
    },

    /**
     * 
     * @param val 
     */
    currentChange (val) {
      if (this.getOrSearch === 0) {
        this.pageNum = val
        getCurrentVehicleServeData(this.pageNum,this.pageSize).then(res => {
          this.vehicleData = res.data.rows
        }).catch(err => {
          console.log(err)
        })
      } else {
        this.searchForm.currentPage = val
        this.getQuery()
      }

    },

    /**
     * 
     * @param val 
     */
    sizeChange (val) {
      if (this.getOrSearch === 0) {
        this.pageSize = val
        getCurrentVehicleServeData(this.pageNum,this.pageSize).then(res => {
          this.vehicleData = res.data.rows
        }).catch(err => {
          console.log(err)
        })
      } else {
        this.searchForm.pageSize = val
        this.getQuery()
      }
    },

    /**
     * 
     */
    getQuery () {
      searchVehicleServeData(this.searchForm).then(res => {
        console.log('se',res.data)
        this.vehicleSearchData = res.data.rows
        this.totalCount = res.data.totalCount
        this.getOrSearch = 1
      }).catch(err => {
        console.log(err)
      })
    },
  }
}
</script>

<style scoped>
  .vehicle-body {
    width: 100%;
    height: auto;
  }
  .vehicle-line {
    display: flex;
    margin-bottom: 10px;
    justify-content: space-between;
    width: 100%;
  }
  .vehicle-page {
    height: 40px;
    font-size: 18px;
    font-weight: 400;
    line-height: 40px;
    color: rgba(0,0,0,.85);
  }
  .vehicle-list {
    box-shadow: 0 2px 4px rgba(0, 0, 0, .12), 0 0 6px rgba(0, 0, 0, .04);
  }

  .vehicle-button {
    display: flex;
    justify-content: space-between;
  }

  .vehicle-button-input {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 1040px;
    margin-right: 50px;
  }

  .el-button--primary /deep/ .el-icon-search {
    margin-right: 5px;
  }

  .input-style {
    padding-top: 3px;
    width: 150px;
  }

  .add-margin {
    margin-left: 10px;
  }
</style>
